.container {
  background-color: #ffffff;
  width: 100%;
  display: flex;
  max-width: 1280px;
  margin: 0px auto;
  height: 60px;
}

.imgContainer {
  height: 60px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 2rem !important;
  padding-right: 2rem !important;
}

.imgContainer img {
  height: 40px;
  width: auto;
}

button img {
  width: 10px;
  height: auto;
}

.inputContainer {
  flex: 1;
  height: fit-content;
  padding-right: 2rem !important;
  margin: auto 0px;
}

.searchIcon {
  padding-left: 1.5rem !important;
  padding-right: 1.5rem !important;
  background-color: #f8f8f8;
}

.searchIcon img {
  height: 100% !important;
}

